<template>
  <div id="app">

    <MainMenu :groups="groups"
              :filterGroup="filterGroup"
              :filterText="filterText"
              :setSelectedGroup="setSelectedGroup"
              :setFilterText="setFilterText"
    />

    <b-container class="bv-example-row">
      <b-row v-if="endpoints.length > 0">
        <b-col sm="10" offset="1">

          <Endpoint v-for="ep in filteredEndpoints"
                    :endpoint="ep"
                    :key="ep.id" />

        </b-col>
      </b-row>
    </b-container>

  </div>
</template>

<script>
import _ from 'lodash'
import MainMenu from './components/MainMenu'
import Endpoint from './components/Endpoint'

export default {
  name: 'App',
  components: {
    MainMenu,
    Endpoint,
  },
  data() {
    return {
      endpoints: [],
      filterGroup: null,
      filterText: null,
    }
  },
  methods: {
    setSelectedGroup(group) {
      this.filterGroup = group
    },
    setFilterText(text) {
      this.filterText = text
      console.log('filterText', text)
    }
  },
  computed: {
    groups() {
      return _.uniq(this.endpoints.map(ep => ep.group))
    },
    filteredEndpoints() {
      let filtered = this.endpoints;
      if (this.filterGroup) {
        filtered = filtered.filter(ep => ep.group === this.filterGroup)
      }
      if (this.filterText) {
        filtered = filtered.filter(ep => {
          return ep.endpoint.indexOf(this.filterText) !== -1
        })
      }
      return filtered;
    }
  },
  mounted: function() {
    fetch('https://smmtouch.store:8443/api/documentor', {
      method: 'get'
    })
    .then( response => {
      // console.log(response)
      return response.json()
    })
    .then( jsonData => {
      this.endpoints = jsonData.endpoints
    })
  }
}
</script>

